.big-thermometer {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: bold;
  font-size: 30px;
}

.big-thermometer-container {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: flex-end;
  margin: 20px;
}

.big-thermometer-indicator-container {
  width: 60px;
  height: 350px;
  border-radius: 30px;
  background-color: #eeeeee;
  margin-bottom: -35px;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  align-items: center;
}

.big-thermometer-indicator {
  background-color: #f34e54;
  width: 50px;
  border-radius: 25px;
  margin-bottom: -25px;
  transition: height 500ms;
}

.big-thermometer-bulb-container {
  width: 125px;
  height: 125px;
  border-radius: 100%;
  margin-bottom: 3px;
  background-color: #eee;
  display: flex;
  justify-content: center;
  align-items: center;
}

.big-thermometer-bulb {
  background-color: #f34e54;
  width: 115px;
  height: 115px;
  border-radius: 100%;
  margin-bottom: -85px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
