.control-moving-container {
  display: flex;
  flex-direction: column;
  align-items: space-between;
  width: 100%;
}
.accordion-style {
  justify-content: space-between;
}
.control-moving-sub-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 99px;
}
.control-moving-sub-header {
  margin-right: 20px;
  display: flex;
}
.control-moving-sub-header input {
  border: 1px solid #777;
  text-align: center;
  width: 70px;
}

.control-moving-sub-header h4 {
  margin: 0;
  font-size: 22px;
  margin-bottom: 7px;
  margin-right: 10px;
}
