.control-heating-container {
  display: flex;
  flex-direction: column;
}
.monitor-heating-container {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
  margin-left: 20px;
}
.gauges {
  display: flex;
  margin: 20px;
  width: 50%;
}

.gauges > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gauges input {
  width: 30px;
  text-align: center;
  border: none;
  border-bottom: 1px solid #000000;
  font-size: 17px;
}
